.App
{
  height: 100vh;
  padding-top: 10px;
  background-color: #1b1b32;
  font-family: Lato, sans-serif;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;

}

.titulo
{
  width: 100%;
  height: 30px;
  margin: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 35px;
  color: rgb(218, 216, 214);
}

a
{
  text-decoration: none;
  color: rgb(218, 216, 214);

}
a:hover
{
  color: darkred;
}

.contenedorCalc
{
  width: 400px;
  height: 600px;
  padding: 14px;
  background-color: #99c9ff;
  border-radius: 20px;
  border: 5px solid white;

}

.fila
{
  margin: 4px;
  display: flex;
  align-items: center;
  justify-content: center;
}